.content {
    height: 550px;

    h1 {
        margin: 0;
    }
}

.container {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    width: 500px;
    margin: 50px auto;
    padding: 50px;
}

.loginFormInput {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    padding: 0 15px;
    width: 300px;
    margin: 50px auto;

    svg {
        width: 22px;
        height: 22px;
    }

    input {
        border: 0;
        padding: 13px;
        font-size: 16px;
        text-align: center;
    }

    :focus::placeholder {
        color: #0F98DA;
        transition: color 0.3s ease-in-out;
    }
}

.blueButton {
    width: 100%;
    height: 55px;
    background: #0F98DA;
    border-radius: 18px;
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &:disabled {
        background-color: #bebebe !important;
        cursor: default;
    }

    &:hover {
        background: lighten(#179ee2, 10%);
    }

    &:active {
        background: darken(#0F98DA, 5%);
    }
}

.register {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 16px;
    opacity: 0.6;
    transition: opacity 0.1s ease-in-out;
    margin-top: 20px;

    &:hover {
        opacity: 1;

    }
}

.overlay {
    visibility: hidden;
    opacity: 0;
    transform: translateX(-200px);
    z-index: -1;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.overlayVisible {
    visibility: visible;
    opacity: 1;
    z-index: 1;
    transform: translateX(0px);

    .cont {
        background-color: rgb(255, 59, 59);
        padding: 10px;
        border-radius: 10px;
        color: white;
        font-weight: bold;
    }
}

.hide {
    visibility: hidden;
}

.cont {
    background-color: rgb(255, 255, 255);
    transition: background-color 0.2s ease-in-out;
}