.panel {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  width: 600px;
  margin: 50px auto;
  animation: opacity 0.5s ease-in-out;


  @keyframes opacity {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  p:not(.confirmBtn) {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    transition: all 0.2s ease-in-out;
    height: 40px;
    margin: 5px;
    display: flex;
    padding-left: 10px;
    align-items: center;
    width: 200px;
    background-color: #ffffff;
    color: #424242;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;

    &:hover {
      color: #1195DE;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.06);
    }

    svg {
      margin-right: 5px;
    }
  }

  img {
    border-radius: 10px;
    object-fit:cover;
    object-position: center;  
  }
}



.fileInput {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
  height: 40px;
  margin: 5px;
  display: flex;
  padding-left: 10px;
  align-items: center;
  width: 200px;
  background-color: #ffffff;
  color: #424242;
  font-size: 18px;
  font-weight: bold;

  .file {
    position: absolute;
    opacity: 0;

  }



  &:hover {
    color: #1195DE;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.06);

  }

  svg {
    margin-right: 5px;
  }
}


.confirmBtn {

  border: 1px solid #bbbbbb;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
  height: 40px;
  margin: 5px;
  display: flex;
  padding-left: 10px;
  align-items: center;
  width: 200px;
  background-color: #ffffff;
  color: #424242;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    color: #0abe91;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.06);
    border: 1px solid #424242;
  }

  svg {
    margin-right: 5px;
  }
}