.left {
    margin-left: 48px;
    margin-top: 62px;

    p {

        font-style: normal;
        font-weight: 700;
        font-size: 38px;
        line-height: 46px;
        margin: 0;
    }

    button {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        text-transform: uppercase;
        margin-top: 46px;
    }
}

.blueButton {
    width: 100%;
    height: 55px;
    background: #0F98DA;
    border-radius: 18px;
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &:disabled {
        background-color: #bebebe !important;
        cursor: default;
    }

    &:hover {
        background: lighten(#179ee2, 10%);
    }

    &:active {
        background: darken(#0F98DA, 5%);
    }
}

.right {

    
    img {
        margin-top: 25px;
        margin-right: 45px;
       
    }

   
}


.slide {
    animation: opacity 0.5s ease-in-out;


   
}

.slide2 {
    animation: opacity 0.5s ease-in-out;


}
@keyframes opacity {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.slider {
    background-color: #E9F1F4;
    max-width: 960px;
    height: 300px;
    margin: 50px auto;
    margin-top: 0;
    border-radius: 20px;
    position: relative;

    svg {
        position: absolute;
        top: 134px;
        right: -25px;

    }


}

.slideLeft {
    position: absolute;
    top: 134px;
    left: -25px;
    width: 45px;
    height: 45px;
}

.slideRight {
    width: 45px;
    height: 45px;
}
