* {
  user-select: none;
  font-family: 'Inter';
}

::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: 'Inter';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(0, 198, 173);
  background: -moz-linear-gradient(90deg, rgba(0, 198, 173, 1) 0%, rgba(0, 170, 198, 1) 25%, rgba(17, 148, 222, 1) 50%, rgba(0, 170, 198, 1) 75%, rgba(0, 198, 173, 1) 100%);
  background: -webkit-linear-gradient(90deg, rgba(0, 198, 173, 1) 0%, rgba(0, 170, 198, 1) 25%, rgba(17, 148, 222, 1) 50%, rgba(0, 170, 198, 1) 75%, rgba(0, 198, 173, 1) 100%);
  background: linear-gradient(90deg, rgba(0, 198, 173, 1) 0%, rgba(0, 170, 198, 1) 25%, rgba(17, 148, 222, 1) 50%, rgba(0, 170, 198, 1) 75%, rgba(0, 198, 173, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00c6ad", endColorstr="#00c6ad", GradientType=1);
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #EAEAEA;

  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}