.card {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #F3F3F3;
    padding: 30px;
    width: 220px;
    border-radius: 40px;
    margin-right: 30px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    margin-bottom: 30px;

    .addedBtn {
        cursor: pointer;
        width: 32px;
        height: 32px;
        margin-left: 32px;
    }

    .plus {
        cursor: pointer;
        margin-left: 32px;
        width: 32px;
     height: 32px;
    }

    .favoriteBtn {
        cursor: pointer;
        margin-left: 5px;
        width: 32px;
        height: 32px;
        
    }

    &:hover {
        box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
        transform: translateY(-5px);
    }

    .favorite {
        width: 32px;
        height: 32px;
        cursor: pointer;
    }

    span {
        font-size: 12px;
        opacity: 0.5;
        text-transform: uppercase;
    }

    b {
        font-size: 13px;
        width: 80px;
    }

    h5 {
        font-weight: 400;
        font-size: 14px;
    }

    .button {
        background: #FFFFFF;
        border: 1px solid #F2F2F2;
        box-sizing: border-box;
        border-radius: 8px;
        height: 32px;
        width: 32px;
        cursor: pointer;
    }
}

svg {
    cursor: pointer;
   
}