.navbar {
    li {
        svg {
            width: 20px;
            height: 20px;
        }
    }
}

header {
    animation: opacity 0.5s ease-in-out;

    li {
        a {
            img {
                object-fit: cover;
                object-position: center;
            }
        }
    }

}


@keyframes opacity {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}