.panel {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    padding: 10px;
    width: 600px;
    margin: 50px auto;
  
    height: 462px;
    animation: opacity 0.5s ease-in-out;

    
    @keyframes opacity {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
    p {
        border: 1px solid #f3f3f3;
        border-radius: 10px;
        transition: all 0.2s ease-in-out;
        height: 40px;
        width: 300px;
        margin: 5px;
        display: flex;
        padding-left: 10px;
        align-items: center;       
        background-color: #ffffff;
        color: #424242;
        font-size: 18px;
        font-weight: bold;
        cursor: pointer;

        &:hover {
            color: #1195DE;
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.06);
        }

        svg {
            margin-right: 5px;
        }
    }

    img {
        border-radius: 10px;
    }

    span {
        color: #1195DE;
        font-weight: bold;
      
        margin-top: 30px;
        margin-bottom: 10px;
        display: flex;
        padding-left: 10px;
        align-items: center; 
        svg {
          margin-right: 5px;
          cursor: default;
      }
    }

    a {
        button {
          width: 245px;
    
          &:hover {
            svg {
              transform: translateX(-5px);
            }
          }
    
          svg {
            width: 18px;
            height: 16px;
            transition: transform 0.15s ease-in-out;
          }
        }
      }
}

.blueButton {
    width: 100%;
    height: 55px;
    background: #0F98DA;
    border-radius: 18px;
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;
  
    &:disabled {
      background-color: #bebebe !important;
      cursor: default;
    }
  
    &:hover {
      background: lighten(#179ee2, 10%);
    }
  
    &:active {
      background: darken(#0F98DA, 5%);
    }
  }

.container{
    svg {
        transition: all 0.2s ease-in-out;
        &:hover {
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.06);
        }
    }
}

.emailFormInput {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  width: 300px;




  input {
      border: 0;
      
      padding: 13px;
      font-size: 16px;
      text-align: center;
      width: 300px;
  }

  :focus::placeholder {
      color: #0F98DA;
      transition: color 0.3s ease-in-out;
  }
}