.overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.1s ease-in-out, visibility 0.1s ease-in-out;
}

.overlayVisible {
    visibility: visible;
    opacity: 1;

    .drawer {
        transform: translateX(0);
        overflow: scroll;
    }
}

.drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 440px;
    height: 100%;
    right: 0;
    background: #FFFFFF;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;

    .items {
        flex: 1;
        overflow: auto;
        margin-bottom: 40px;
    }

    h2 {
        margin: 0;

    }
}

.cartItem {
    border: 1px solid #f3f3f3;
    border-radius: 20px;
    overflow: hidden;
    padding: 20px;

    p {
        font-size: 16px;
        margin: 0;
    }

    b {
        font-size: 14px;
    }

    .removeBtn {
        opacity: 0.5;
        cursor: pointer;
        transition: opacity 0.15s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }
}

.blueButton {
    width: 100%;
    height: 55px;
    background: #0F98DA;
    border-radius: 18px;
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &:disabled {
        background-color: #bebebe !important;
        cursor: default;
    }

    &:hover {
        background: lighten(#179ee2, 10%);
    }

    &:active {
        background: darken(#0F98DA, 5%);
    }
}

.cartTotalBlock {
    ul {
        display: block;
        margin-bottom: 40px !important;

        li {
            display: flex;
            align-items: flex-end;
            margin-bottom: 20px;

            div {
                flex: 1;
                height: 1px;
                border-bottom: 1px dashed #dfdfdf;
                position: relative;
                top: -4px;
                margin: 0 7px;
            }
        }
    }

    .blueButton {
        position: relative;


        &:disabled {
            animation: button-loading 0.8s ease-in-out infinite;
        }

        &:hover {
            svg {
                transform: translateX(5px);
            }
        }

        svg {
            position: absolute;
            right: 30px;
            top: 20px;
            transition: transform 0.15s ease-in-out;
            width: 18px;
            height: 16px;
        }
    }
}