.ordersPage {
    margin: 50px auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    a {
        button {
            width: 245px;

            &:hover {
                svg {
                    transform: translateX(-5px);
                }
            }

            svg {
                width: 18px;
                height: 16px;
                transition: transform 0.15s ease-in-out;
            }
        }
    }


    h1 {
        font-size: 24px;
    }

    p {
        font-size: 16px;
        max-width: 290px;
    }
}

.blueButton {
    width: 100%;
    height: 55px;
    background: #0F98DA;
    border-radius: 18px;
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &:disabled {
        background-color: #bebebe !important;
        cursor: default;
    }

    &:hover {
        background: lighten(#179ee2, 10%);
    }

    &:active {
        background: darken(#0F98DA, 5%);
    }
}