.content {
    h1 {
        margin: 0;
    }

    animation: opacity 0.5s ease-in-out;


    @keyframes opacity {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

}

.searchBlock {
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    padding: 0 15px;
    position: relative;

    svg {
        width: 14.25px;
        height: 14.25px;
    }

    .clear {
        position: absolute;
        right: 0;
        width: 18px;
        height: 18px;
        top: 14px;
        right: 15px;
        cursor: pointer;
    }

    input {
        border: 0;
        padding: 13px;
        font-size: 16px;
        width: 200px;
    }
}

.slide {
    animation: opacity 0.5s ease-in-out;


    @keyframes opacity {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }
}