.cartEmpty {
    text-align: center;

    p {
        width: 280px;
        line-height: 24px;
    }

    .blueButton {
        width: 100%;
        height: 55px;
        background: #0F98DA;
        border-radius: 18px;
        border: none;
        color: #fff;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        transition: background 0.1s ease-in-out;
      
        &:disabled {
          background-color: #bebebe !important;
          cursor: default;
        }
      
        &:hover {
          background: lighten(#179ee2, 10%);
        }
      
        &:active {
          background: darken(#0F98DA, 5%);
        }
      }
    .blueButton:hover img {
        transform: translateX(-3px);
    }

    .blueButton img {
        position: relative;
        top: 1px;
        margin-right: 15px;
        -webkit-transition: -webkit-transform 0.15s ease-in-out;
        transition: -webkit-transform 0.15 ease-in-out;
        transition: transform 0.15s ease-in-out;
        transition: transform 0.15s ease-in-out, -webkit-transform 0.15s ease-in-out;
    }
}